.formInput {
    display: flex;
    flex-direction: column;
    width: 280px;
}

.formInput input {
    padding: 15px;
    margin: 10px 0px;
    border-radius: 10px;
    border: 1px solid gray;
}

.formInput span {
    font-size: 12px;
    padding: 3px;
    color: red;
    display: none
}

input:invalid[focused="true"] ~ span {
    display: block;
}

input:invalid[focused="true"] {
    border: 1px solid red;
}
